@import "src/Common/Styles/mixins";

.projectsBlock {
  display: flex;
  justify-content: center;

  .presentBlock {
    @include commonContainer;
    display: flex;
    flex-direction: column;

    .header {
      font-size: 16pt;
      font-weight: bold;
    }

    .body {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
    }
  }
}









